import { Component } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { map } from "rxjs/operators";

@Component({
  selector: 'feed',
  templateUrl: './feed.component.html',
  styleUrls: ['./feed.component.css']
})
export class FeedComponent{
  title = 'feed';
  apiRoot: string = "http://localhost:3000/api/post?page=1&limit=15";
  data: any = {};

  constructor(private http: HttpClient) {
    this.getPosts();
    this.getData();
  }

  getData() {
    console.log("GET WITH HEADERS");
    return this.http.get(this.apiRoot, {
      headers:
        new HttpHeaders()
          .append('Access-Control-Allow-Methods', 'GET,PUT,POST,DELETE,PATCH,OPTIONS')
          .append('Access-Control-Allow-Origin', 'http://localhost:4200')
          .append('Access-Control-Allow-Headers', "Access-Control-Allow-Headers, Access-Control-Allow-Origin, Access-Control-Request-Method")
          .append('Authorization', "Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjViM2NiZDVkM2M1YzY5MGE0MTU1ODc5NiIsImlhdCI6MTUzMDcxMTAzMCwiZXhwIjoxNTMwNzk3NDMwfQ.uNVXvZ9lSSK_Ty3nmaIUPAAFG0pT6sxRGcJHbh5Yhh4")
      , responseType: 'text'
    })
    .pipe(map(data => data));
  }

  getPosts() {
    this.getData().subscribe(data => {
      console.log(data);
      this.data = data;
    })
  }


}
